<template>
  <div class="app">
    <musicList :title="title" :bg-image="bgImage" :songs="songs"></musicList>
  </div>

</template>

<script type="text/ecmascript-6">
import { mapGetters } from "vuex";
// import { ERR_OK } from "api/config";
import musicList from "base/musicList/musicList";
import { getSongList, recommodSong } from "api/recommend";
import { createSong } from "assets/js/song";
// import { recomSongList } from "assets/js/song";
const ERR_OK = 200;
export default {
  components: {
    musicList
  },
  computed: {
    title() {
      return this.disc.name;
    },
    bgImage() {
      return this.disc.coverImgUrl;
    },
    ...mapGetters(["disc"])
  },
  data() {
    return {
      songs: [],
      recommodsons: []
    };
  },
  created() {
    this._getSongList();
    console.log(this.disc);
  },
  methods: {
    _getRecommodSong() {
      recommodSong().then(res => {
        console.log(res);
        if (res.code === ERR_OK) {
          this.recommodsons = res.recommend;
          // this.songs = this.recommodsons;
          this.mapdata();
        }
      })
    },
    mapdata() {
      this.recommodsons.map((item, index) => {
        this.songs.push({
          id: item.id,
          name: item.name,
          songfalg: 'like',
          ar: [{
            name: item.artists[0].name,
            picUrl: item.album.picUrl
          }],
          al: {
            picUrl: item.album.picUrl,
            name: item.artists[0].name
          }
        })
      })
    },
    // 获取歌单歌曲
    _getSongList() {
      var that = this;
      if (!this.disc.id) {
        this.$router.push("/recommend");
        return;
      }
      if (this.disc.id === 5201314) {
        console.log('123123123');
        this._getRecommodSong();
      } else {
        getSongList(this.disc.id).then(res => {
          console.log(res);
          // let ret = recomSongList(res);
          if (res.code === ERR_OK) {
            // that.songs = that._normalSongs(res.playlist.tracks);
            that.songs = res.playlist.tracks;
          }
        });
      }
    },
    _normalSongs(list) {
      let ret = [];
      list.forEach(musicData => {
        if (musicData.songid && musicData.albummid) {
          // 必须有id和连接码
          ret.push(createSong(musicData));
        }
      });
      return ret;
    }
  }
};
</script>

<style scoped lang="scss">
@import "assets/css/mixin.scss";
.app {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgb(34, 34, 34);
  z-index: 301;
}
</style>



